<template>
    <div>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-top:15px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="姓名"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers">查询</el-button>
                </el-form-item>
                <!-- <el-form-item>
                    <el-button type="primary" @click="addUser">新增用户</el-button>
                </el-form-item> -->
                <!-- <router-link to="/user/import">
                    <el-button type="primary">导入用户</el-button>
                </router-link> -->
                <!-- <el-button type="primary" @click="exportWord">导出<i class="el-icon-upload el-icon--right"></i></el-button> -->
            </el-form>
        </el-col>


        <el-table :data="tableData()" style="width: 100%" >
            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="phone" label="电话" width="180">
            </el-table-column>
            <el-table-column prop="password" label="密码" width="180">
            </el-table-column>
            <el-table-column prop="statusName" label="级别" width="180">
            </el-table-column>
            <!--第二步  开始进行修改和查询操作-->
            <el-table-column label="操作" align="center" min-width="100">

                <template slot-scope="scope">
                    <el-button type="primary" @click="change(scope.row)">设为管理员</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>

</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
    export default {
        name: 'home',
        inject:['reload'],
        data() {
            return {
                userInfoList: [],
                
                filters: {
                    name: ''
                },
                token:'',
                user:{}
            }
        },

        methods: {
            //  exportWord() {
            //     this.$exportExcel({
            //         url: "/user/exportExcel",
            //         data: {},
            //         responseType: "blob",
            //         fileName: "用户记录",
            //         method: 'post'
            //     });
            // },
            loadData() {
                // let param = {filter:this.filters.name};
                this.axios.get(this.$baseUrl()+'/user/list').then((res) => {
                    console.log(res);                    
                    this.userInfoList = res.data.data;
                });

            },
             tableData() {
                let status = {
                    0: "用户",
                    1: "管理员"
                };
                return this.userInfoList.map(it => {
                    it.statusName = status[it.level];
                    return it;
                });
            },
            getUsers() {
                this.axios.get("user/findByNameLike",{
                    params:{
                        name:this.filters.name
                    }
                }).then(res=>{
                    console.log("res:",res);
                    this.userInfoList = res.data.data;
                })
            },
            // addUser(){

            // },
            change(row) {
                console.log("id: ",row.id);
                this.axios.get("user/setAdmin/"+row.id).then(res=>{
                    console.log("res: ",res);
                    if(res.data.code===200){
                        this.$message({
                            message:"设置管理员成功",
                            type:'success'
                        })
                        this.reload();
                    }else{
                            this.$message({
                                message: res.data.message,
                                type:'error'
                            });
                            
                        this.reload();
                    }
                })
            },
           
        },
        created(){
            this.user = this.$store.state.user.accessToken;
            console.log("user: ",this.user);
            
            this.loadData();
        }
    }
</script>

<style>
body {
        background: #DFE9FB;
    }
.demo-ruleForm{
  margin-left: 10%;  
  width: 80%;
}
.el-table .success-row {
    background: #f0f9eb;
  }
</style>


